
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

p {
    font-size: 1.6rem;
}

h1 {
    font-size: 6rem;
}

.app {
    width: 100%;
    height: 100%;
    position: relative;
    background-color: rgba(0, 0, 0, 0.4);
    color: #fff;
}

@-webkit-keyframes clr {
    0% {
        background: url(/weather_app/static/media/field-533541_1280.43977edb.jpg) no-repeat center center/cover;
    }
    20% {
        background: url(/weather_app/static/media/sunset-1373171_1920.12f2ee4d.jpg) no-repeat center center/cover;
    }
    40% {
        background: url(/weather_app/static/media/sea-164989_1280.ad34399b.jpg) no-repeat center center/cover;
    }
    60% {
        background: url(/weather_app/static/media/sunrise-1014712_1280.39ef3930.jpg) no-repeat center center/cover;
    }
    80% {
        background: url(/weather_app/static/media/tree-736885_1280.d231c1db.jpg) no-repeat center center/cover;
    }
    100% {
        background: url(/weather_app/static/media/mountains-190055_1280.7e016717.jpg) no-repeat center center/cover;
    }
}

@keyframes clr {
    0% {
        background: url(/weather_app/static/media/field-533541_1280.43977edb.jpg) no-repeat center center/cover;
    }
    20% {
        background: url(/weather_app/static/media/sunset-1373171_1920.12f2ee4d.jpg) no-repeat center center/cover;
    }
    40% {
        background: url(/weather_app/static/media/sea-164989_1280.ad34399b.jpg) no-repeat center center/cover;
    }
    60% {
        background: url(/weather_app/static/media/sunrise-1014712_1280.39ef3930.jpg) no-repeat center center/cover;
    }
    80% {
        background: url(/weather_app/static/media/tree-736885_1280.d231c1db.jpg) no-repeat center center/cover;
    }
    100% {
        background: url(/weather_app/static/media/mountains-190055_1280.7e016717.jpg) no-repeat center center/cover;
    }
}

.app:before {
    -webkit-animation: clr 60s infinite;
            animation: clr 60s infinite;
    content: '';
    /*background: url("./assets/sunset-1373171_1920.jpg") no-repeat center center/cover;*/
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
}

.app .search {
    text-align: center;
    padding: 1rem;
}

.app input {
    padding: .7rem 1.5rem;
    font-size: 1.2rem;
    border-radius: 25px;
    border: 1px solid rgba(255, 255, 255, 0.8);
    background: rgba(255, 255, 255, 0.1);
    color: #f8f8f8;
}

::-webkit-input-placeholder {
    color: #f8f8f8;
}

:-ms-input-placeholder {
    color: #f8f8f8;
}

::placeholder {
    color: #f8f8f8;
}

.container {
    max-width: 100%;
    min-height: 100vh;
    margin: auto;
    padding: 0 1rem;
    position: relative;
    top: 10%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.app .top {
    width: 100%;
    margin: 1rem auto;
}

.app .description {
    position: relative;
    right: -90%;
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
    -webkit-transform: rotate(269deg);
            transform: rotate(269deg);
}

.app .bottom {
    display: flex;
    justify-content: space-evenly;
    text-align: center;
    width: 100%;
    margin: 1rem auto;
    padding: 1rem;
    border-radius: 12px;
    background-color: rgba(255, 255, 255, 0.2);
}

.bold {
    font-weight: 700;
}
